import React from "react";
import CiteRow from "./CiteRow";
import { TableBody } from "@material-ui/core";

const CitesTableBody = ({ rows }) => {
  return (
    <TableBody>
      {rows().map((row, index) => (
        <CiteRow row={row} key={index} />
      ))}
    </TableBody>
  );
};

export default CitesTableBody;
