//nombres de las filas
export let tableHeaders = [
  "date",
  "cite",
  "requested",
  "addressed",
  "signed",
  "reference",
];
//devuelve un booleano de si la fila pasa o no el filtro
function filterRow(row, filter) {
  let cont = 0;
  tableHeaders.forEach((element) => {
    row[element].includes(filter) && cont++;
  });
  return cont !== 0;
}
//Funcion que devuelve
export const filterRows = ({ data, filter, page, rowsPerPage, setCount }) => {
  // let filterRows = data.slice();
  filter !== "" &&
    (data = data.filter((element) => filterRow(element, filter)));
  setCount(data.length);
  const start = page * rowsPerPage;
  const end = start + rowsPerPage;
  return data.slice(start, end);
};
// Devuelve los datos de paginacion de la tabla
export function pagination({
  count,
  page,
  setPage,
  rowsPerPage,
  setRowsPerPage,
}) {
  return {
    count: count,
    page: { get: page, set: setPage },
    rowsPerPage: { get: rowsPerPage, set: setRowsPerPage },
  };
}

