import React from "react";
import { TableHead, TableRow, TableCell } from "@material-ui/core";
let head = [
  "Fecha",
  "Cite",
  "Solicitado por",
  "Firmado Por",
  "Dirigido a",
  "Referencia",
];
const CitesTableHead = () => {
  return (
    <TableHead>
      <TableRow></TableRow>
      <TableRow>
        {head.map((el, index) => (
          <TableCell key={index}>{el}</TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
};

export default CitesTableHead;
