import React from "react";
import { TableRow, TableCell } from "@material-ui/core";
import { tableHeaders } from "./cites";

const CiteRow = ({ row }) => {
  return (
    <TableRow>
      {tableHeaders.map((name) => (
        <TableCell key={name}>{row[name]}</TableCell>
      ))}
    </TableRow>
  );
};

export default CiteRow;
