import React, { useState } from "react";
import { Paper, TableContainer, Table } from "@material-ui/core";
import SearchToolbar from "./SearchToolbar";
import CitesPagination from "./CitesPagination";
import CitesTableHead from "./CitesTableHead";
import CitesTableBody from "./CitesTableBody";
import { filterRows, pagination, data } from "./cites";

function CitesTable() {
  const [filter, setFilter] = useState("");
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [count, setCount] = useState(data.length);
  let paginationArgs = { count, page, setPage, rowsPerPage, setRowsPerPage };
  let filterArgs = { data, filter, page, rowsPerPage, setCount };

  return (
    <Paper>
      <SearchToolbar filter={{ get: filter, set: setFilter }} />
      <TableContainer>
        <Table>
          <CitesTableHead />
          <CitesTableBody rows={() => filterRows(filterArgs)} />
          <CitesPagination {...pagination(paginationArgs)} />
        </Table>
      </TableContainer>
    </Paper>
  );
}
export default CitesTable;