import React from "react";
import SearchIcon from "@material-ui/icons/Search";
import {
  Toolbar,
  Typography,
  OutlinedInput,
  FormControl,
  InputAdornment,
  Grid,
  IconButton,
} from "@material-ui/core";
const SearchToolbar = ({ filter }) => {
  const handleFilter = (event) => {
    filter.set(event.target.value);
  };
  return (
    <Toolbar component={Grid} container justify="space-between">
      <Typography variant="h6" component={Grid} item xs={12} sm={4}>
        Lista de Cites
      </Typography>
      <FormControl component={Grid} item xs={12} sm={3}>
        <OutlinedInput
          value={filter.get}
          onChange={handleFilter}
          placeholder="Buscar"
          endAdornment={
            <InputAdornment position="end">
              <IconButton>
                <SearchIcon />
              </IconButton>
            </InputAdornment>
          }
        ></OutlinedInput>
      </FormControl>
    </Toolbar>
  );
};

export default SearchToolbar;
